import React, { useState, useEffect } from 'react'
import { useHistory, useLocation } from 'react-router-dom'
import { ExclamationCircleOutlined } from '@ant-design/icons'
import { Modal, notification, Pagination,Select } from 'antd'
import ReactJson from 'react-json-view'


import {
  CForm,
  CFormText,
  CCardFooter,
  CAlert,
  CInputFile,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CDataTable,
  CFormGroup,
  CLabel,
  CRow,
  CInput,
  CTextarea,
  CButton,
} from '@coreui/react'
import config from '../../config'
import axios from 'axios'

const Users = () => {
  const history = useHistory()
 
  const [usersKycData, setUserData] = useState([])
  const [success, setSuccess] = useState(false)
  const [error, setError] = useState(false)
  const [viewModal, setViewModal] = useState(false)
  const [kycInfo, setKycInfo] = useState([])
  const [load, setLoad] = useState(false)
  const [verificationReasonsDatas, setVerificationReasonsData] = useState([])
  const [declineModal, setDecline] = useState(false)
  const [reason, setReason] = useState('')
  const [userId, setUserId] = useState('')
  const [loadView, setLoadView] = useState(false)
  const [providerDetails, setProviderDetails] = useState({})
  const [msg, setMsg] = useState('')
  const [fill, setFill] = useState(false)
  const [reasonsMulti, setReasonsMutli] = useState("")
  const [viewProviderModal, setViewProviderModal] = useState(false)

  const closeViewProvider = () => {
    setViewProviderModal(false)

  }


  const [page, setPage] = useState(1)
  const [totalItems, setTotalItems] =  useState("")



const { Option } = Select;

const verificationReasonSelect = []
  verificationReasonsDatas.map(reason => {
    verificationReasonSelect.push(<Option key={reason}>{reason.replace("_", " ")}</Option>)
  })


function handleChange(value) {
  setReasonsMutli(value)
}




  const getKycPaged = queryString => {
      axios.get(`${config.baseUrl}/api/v1/admin/traderx-users/all?${queryString}`, {
        headers: {
          Authorization: `Bearer ${sessionStorage.getItem('token')}`
        }
      })
      .then(res => {
        if (res.status === 200) {
           setUserData(
            res.data.records.map(data => ({
              code: data.code,
              createdOn:  data.createdOn ? data.createdOn.slice(0, 10) : "",
              firstName: data.firstName,
               lastName: data.lastName,
               emailAddress: data.emailAddress,
               country: data.country,
               phoneNumber:data.phoneNumber,
               accountType: data.accountType,
               identityVerificationStatus: data.identityVerificationStatus,
               status: data.status,
            }))
          )
          setTotalItems(res.data.totalPages * 10)
        }
      })
      .catch(err => {
        if (err) {
        }
      })
  }


  const verificationReasons = () => {
    axios.get(`${config.baseUrl}/api/v1/configurations`, {
      headers: {
        Authorization: `Bearer ${sessionStorage.getItem('token')}`
      }
    })
    .then(res => {
      if (res.status === 200) {
        setVerificationReasonsData(res.data.verificationFailureReasonTypes)
      }
    })
    .catch(err => {
      if (err) {
      }
    })
}


  const pagination = (page, pageSize) => {
    console.log(page)
    setPage(page)
    const queryString = `pageNumber=${page}&pageSize=10&accountType=&country=&identityVerificationStatus=NOT_ACCEPTED`
    getKycPaged(queryString)
  }

  const closeDecline = () => {
    setDecline(false)
    setReason('')
    setReasonsMutli("")
  }

  const openDecline = id => {
    setUserId(id)
    setDecline(true)
    setReason('')
  }

  const closeView = () => {
    setViewModal(false)
  }

  useEffect(() => {
    getUsersKyc()
    verificationReasons()
  }, [])

  const viewProviderFunc = () => {
    setViewProviderModal(true)
  }

  const getUsersKyc = () => {
    axios
      .get(
        `${config.baseUrl}/api/v1/admin/traderx-users/kyc?pageNumber=1&pageSize=10&accountType=&country=&identityVerificationStatus=NOT_ACCEPTED`,
        {
          headers: {
            Authorization: `Bearer ${sessionStorage.getItem('token')}`
          }
        }
      )
      .then(res => {
        if (res.status === 200) {
           setUserData(
            res.data.records.map(data => ({
              code: data.code,
              createdOn:  data.createdOn ? data.createdOn.slice(0, 10) : "",
              firstName: data.firstName,
               lastName: data.lastName,
               phoneNumber:data.phoneNumber,
               emailAddress: data.emailAddress,
               country: data.country,
               accountType: data.accountType,
               identityVerificationStatus: data.identityVerificationStatus,
               status: data.status,
            }))
          )
          setTotalItems(res.data.totalPages * 10)
        }
      })
      .catch(err => {
        if (err) {
        }
      })
  }
  const changeStatusConfirm = (id, e) => {
    e.preventDefault()
    Modal.confirm({
      title: `Are you sure you want to approve this user?`,
      icon: <ExclamationCircleOutlined />,
      okText: 'Yes',
      okType: 'danger',
      cancelText: 'No',
      onOk () {
        approve(id.code)
      },
      onCancel () {
        console.log('Cancel')
      }
    })
  }
  const approve = (id, e) => {
    console.log(id)
    setLoad(true)
    axios
      .put(`${config.baseUrl}/api/v1/admin/verification/approve/${id}`,{}, {
        headers: {
          Authorization: `Bearer ${sessionStorage.getItem('token')}`
        }
      })
      .then(res => {
        setLoad(false)
        if (res.status === 200) {
          setSuccess(true)
          setMsg('User Kyc Approved Successfully')
          getUsersKyc()
          setError(false)
          setTimeout(()=>{
            setSuccess(false)
          },2000)
        }
      })
      .catch(err => {
        setLoad(false)
        setSuccess(false)
        if (err.response !== undefined) {
          setMsg(err.response.data.message)
          setError(true)
          setTimeout(() => {
            setError(false)
          }, 2500)
        } else {
          setMsg('Connection Error')
          setError(true)
          setTimeout(() => {
            setError(false)
          }, 2500)
        }
      })
  }

  const decline = (id, e) => {
    if(reason){
    setLoad(true)
    axios
      .put(
        `${config.baseUrl}/api/v1/admin/verification/decline/${userId.code}`,
        {
          reason: reason,
          "verificationTypeReasons" : reasonsMulti

        },
        {
          headers: {
            Authorization: `Bearer ${sessionStorage.getItem('token')}`
          }
        }
      )
      .then(res => {
        setLoad(true)
        setReason('')
        if (res.status === 200) {
          setSuccess(true)
          setLoad(false)
          setTimeout(() => {
            setSuccess(false)
          },3000)
          setReasonsMutli("")
          setMsg('User Kyc Declined Successfully')
          getUsersKyc()
          setError(false)
          setDecline(false)
        }
      })
      .catch(err => {
        setLoad(false)
        setSuccess(false)
        if (err.response !== undefined) {
          setMsg(err.response.data.message)
          setError(true)
          setTimeout(() => {
            setError(false)
          }, 2500)
        } else {
          setMsg('Connection Error')
          setError(true)
        }
      })
    }
    else{
      setFill(true)
      setTimeout(() => {
        setFill(false)
      },2500)
    }
  }

  const view = (id, e) => {
    console.log(id)
    setLoadView(true)
    axios
      .get(`${config.baseUrl}/api/v1/admin/verification/details/${id.code}`, {
        headers: {
          Authorization: `Bearer ${sessionStorage.getItem('token')}`
        }
      })
      .then(res => {
        setLoad(false)
        if (res.status === 200) {
          const infoProvider = res.data.providerKycResponseData ? JSON.parse(res.data.providerKycResponseData) : false
          console.log(infoProvider)
          setViewModal(true)
          setProviderDetails(infoProvider)
          setKycInfo(res.data)
        }
      })
      .catch(err => {
        setLoad(false)
        Notification('error', 'Error', 'Connection Error')
        if (err) {
        }
      })
  }

  const Notification = (type, msgType, msg) => {
    notification[type]({
      message: msgType,
      description: msg
    })
  }

  return (
    <CRow>
      <CCol>
        {success && <CAlert color='success'>{msg}</CAlert>}

        {error && <CAlert color='danger'>{msg}</CAlert>}
        <CCard>
          <CCardHeader>KYC</CCardHeader>
          <CCardBody>
            <CDataTable
              items={usersKycData}
              fields={[
                {
                  key: 'code',
                  name: 'Traderx Code'
                },
                { key: 'createdOn', name: 'Registered Date' },

                {
                  key: 'firstName',
                  name: 'First Name'
                },
                {
                  key: 'lastName',
                  name: 'Last Name'
                },
                {
                  key: 'emailAddress',
                  name: 'Email Address'
                },
                {
                  key: 'phoneNumber',
                  name: 'Phone Number'
                },
                {
                  key: 'country',
                  name: 'Country'
                },
                {
                  key: 'accountType',
                  name: 'Account Type'
                },
              
                {
                  key: 'Actions',
                  name: 'Actions'
                }
              ]}
            
              scopedSlots={{
                Actions: item => (
                  <td className='d-flex'>
                    <button
                      type='button'
                      class='btn btn-danger mr-2'
                      onClick={openDecline.bind(this, item)}
                    >
                      Decline
                    </button>
                 
                    <button
                      type='button'
                      class='btn btn-primary'
                      onClick={view.bind(this, item)}
                    >
                     
                        VIew Details
                    </button>
                  </td>
                )
              }}
            />
           <div className='text-center pagination-part'>
        <Pagination
          current={page}
          total={totalItems}
          defaultPageSize={10}
          showTotal={(total, range) => `${range[0]}-${range[1]} of ${total} items`}
          onChange={pagination}
        />
      </div>
          </CCardBody>
        </CCard>
      </CCol>

      <Modal
        title={'Decline User'}
        visible={declineModal}
        footer={null}
        maskClosable={false}
        onCancel={closeDecline}
      >
        <div className='container'>
          {success && <CAlert color='success'>{msg}</CAlert>}

          {error && <CAlert color='danger'>{msg}</CAlert>}
          {fill && <p className="text-danger text-center">Reason is required </p>}
          <CRow>
            <CCol xs='12'>
              <CFormGroup>
                <CLabel htmlFor='name'>Reason for Declining</CLabel>
                <CTextarea
                  name='textarea-input'
                  id='textarea-input'
                  rows='6'
                  onChange={e => setReason(e.target.value)}
                  value={reason}
                />{' '}
              </CFormGroup>
            </CCol>
          </CRow>
          <br />
          <label>Verification Type Reason</label>
          <Select
      mode="multiple"
      allowClear
      style={{ width: '100%' }}
      placeholder="Please select"
      value={reasonsMulti}
      defaultValue={[]}
      onChange={handleChange}
    >
      {verificationReasonSelect}
    </Select>
    <br />
    <br/>
   

          <CButton block color='danger' onClick={decline}>
            {load ? (
              <div
                class='spinner-border'
                role='status'
                style={{ width: '1rem', height: '1rem' }}
              >
                <span class='sr-only'>Loading...</span>
              </div>
            ) : (
              'Decline'
            )}
          </CButton>
        </div>
      </Modal>

      <Modal
        title={'Provider Details'}
        visible={viewProviderModal}
        footer={null}
        maskClosable={false}
        onCancel={closeViewProvider}
        width={600}
      >
        <ReactJson src={providerDetails} />

          

        </Modal>


        <Modal
        title={'View Kyc Details'}
        visible={viewModal}
        footer={null}
        maskClosable={false}
        onCancel={closeView}
        width={800}
      >
        <div className='container'>
          <CRow>
            <CCol xs='12' md='12'>
              <CCard>
                <CCardHeader>Kyc Information</CCardHeader>
                <CCardBody>
                  <CForm
                    action=''
                    method='post'
                    encType='multipart/form-data'
                    className='form-horizontal'
                  >
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel>Email</CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                          {kycInfo.emailAddress ? kycInfo.emailAddress : 'N/A'}
                        </p>
                      </CCol>
                    </CFormGroup>
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='text-input'>Street Address</CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                          {kycInfo.streetAddress ? kycInfo.streetAddress : 'N/A'}
                        </p>
                      </CCol>
                    </CFormGroup>
                    
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='email-input'>City</CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                          {kycInfo.city ? kycInfo.city : 'N/A'}
                        </p>
                      </CCol>
                    </CFormGroup>
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='email-input'>Postal Code</CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                          {kycInfo.postalCode ? kycInfo.postalCode : 'N/A'}
                        </p>
                      </CCol>
                    </CFormGroup>
                   
                  
                    
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='password-input'>State</CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                          {kycInfo.state ? kycInfo.state : 'N/A'}
                        </p>
                      </CCol>
                    </CFormGroup>
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='date-input'>Date of Birth</CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                        {kycInfo.dateOfBirth ?  new Date(kycInfo.dateOfBirth).toLocaleDateString() : 'N/A'}                        </p>
                        
                      </CCol>
                    </CFormGroup>
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='disabled-input'>Occupation</CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                          {kycInfo.occupation ? kycInfo.occupation : 'N/A'}
                        </p>
                      </CCol>
                    </CFormGroup>
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='disabled-input'>BVN </CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                          {kycInfo.bankVerificationNumber
                            ? kycInfo.bankVerificationNumber
                            : 'N/A'}
                        </p>
                      </CCol>
                    </CFormGroup>
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='disabled-input'>SSN </CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                          {kycInfo.socialSecurityNumber
                            ? kycInfo.socialSecurityNumber
                            : 'N/A'}
                        </p>
                      </CCol>
                    </CFormGroup>
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='disabled-input'>
                          Provider Approval Status{' '}
                        </CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                          {kycInfo.providerKycApprovalStatus
                            ? kycInfo.providerKycApprovalStatus
                            : 'N/A'}
                        </p>
                      </CCol>
                    </CFormGroup>
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='disabled-input'>
                          Provider Message
                        </CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                          {providerDetails.message
                            ? providerDetails.message
                            : 'N/A'}
                        </p>
                      </CCol>
                    </CFormGroup>
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='disabled-input'>Kyc Status </CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                          {providerDetails.status ? providerDetails.status : 'N/A'}
                        </p>
                      </CCol>
                    </CFormGroup>
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='disabled-input'>Identity Type</CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                          {kycInfo.identificationType
                            ? kycInfo.identificationType
                            : 'N/A'}
                        </p>
                      </CCol>
                    </CFormGroup>
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='disabled-input'>Contact Person Name</CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                          {kycInfo.contactPersonFirstName 
                            ? kycInfo.contactPersonFirstName +  ' ' + kycInfo.contactPersonLastName
                            : 'N/A'}
                        </p>
                      </CCol>
                    </CFormGroup>
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='disabled-input'>Contact Person Phonenumber</CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                          {kycInfo.contactPersonPhoneNumber
                            ? kycInfo.contactPersonPhoneNumber : 'N/A'}
                        </p>
                      </CCol>
                    </CFormGroup>
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='disabled-input'>Business Name</CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                          {kycInfo.businessName
                            ? kycInfo.businessName
                            : 'N/A'}
                        </p>
                      </CCol>
                    </CFormGroup>
                   
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='email-input'>Business Type</CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                          {kycInfo.businessType ? kycInfo.businessType : 'N/A'}
                        </p>
                      </CCol>
                    </CFormGroup>
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='email-input'>Business Website</CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                          {kycInfo.businessWebsite ? kycInfo.businessWebsite : 'N/A'}
                        </p>
                      </CCol>
                    </CFormGroup>
                 
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='disabled-input'>Contact Person BVN</CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                          {kycInfo.contactPersonBankVerificationNumber
                            ? kycInfo.contactPersonBankVerificationNumber
                            : 'N/A'}
                        </p>
                      </CCol>
                    </CFormGroup>
                       
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='disabled-input'>TIN</CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                          {kycInfo.taxIdentificationNumber
                            ? kycInfo.taxIdentificationNumber
                            : 'N/A'}
                        </p>
                      </CCol>
                    </CFormGroup>
                   
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='disabled-input'>Identity Type</CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <p className='form-control-static'>
                          {kycInfo.identificationType
                            ? kycInfo.identificationType
                            : 'N/A'}
                        </p>
                      </CCol>
                    </CFormGroup>
                    <br />
                    <br />
                    {kycInfo.verificationType !== "KYB" &&
                    <>

                    {kycInfo.passport &&
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='disabled-input'>Passport</CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <div
                          style={{
                            border: '1px solid dotted',
                            color: '#000000'
                          }}
                        >
                          <img
                            width='100%'
                            height='500'
                            src={`data:image/png;base64,${
                              kycInfo.passport ? kycInfo.passport : 'N/A'
                            }`}
                          />
                        </div>
                      </CCol>
                    </CFormGroup>}
                    <br />
                    <br />
                    
                    {kycInfo.identificationDocument &&
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='disabled-input'>
                          Identification Document{' '}
                        </CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <div
                          style={{
                            border: '1px solid dotted',
                            color: '#000000'
                          }}
                        >
                          <img
                            width='100%'
                            height='500'
                            src={`data:image/png;base64,${
                              kycInfo.identificationDocument
                                ? kycInfo.identificationDocument
                                : 'N/A'
                            }`}
                          />
                        </div>{' '}
                      </CCol>
                    </CFormGroup>}

                    {kycInfo.driversLicence &&
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='disabled-input'>
                          Driver's License{' '}
                        </CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <div
                          style={{
                            border: '1px solid dotted',
                            color: '#000000'
                          }}
                        >
                          <img
                            width='100%'
                            height='500'
                            src={`data:image/png;base64,${
                              kycInfo.driversLicence
                                ? kycInfo.driversLicence
                                : 'N/A'
                            }`}
                          />
                        </div>{' '}
                      </CCol>
                    </CFormGroup>}
                    </>}

                    {kycInfo.verificationType === "KYB" &&
                    <>
                    {kycInfo.cacDocument &&
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='disabled-input'>CAC Document</CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <div
                          style={{
                            border: '1px solid dotted',
                            color: '#000000'
                          }}
                        >
                          <img
                            width='100%'
                            height='500'
                            src={`data:image/png;base64,${
                              kycInfo.cacDocument ? kycInfo.cacDocument : 'N/A'
                            }`}
                          />
                        </div>
                      </CCol>
                    </CFormGroup>}
                    <br />
                    <br />
                    
                    {kycInfo.contactPersonPassport &&
                    <CFormGroup row>
                      <CCol md='3'>
                        <CLabel htmlFor='disabled-input'>
                          Contact Person Passport{' '}
                        </CLabel>
                      </CCol>
                      <CCol xs='12' md='9'>
                        <div
                          style={{
                            border: '1px solid dotted',
                            color: '#000000'
                          }}
                        >
                          <img
                            width='100%'
                            height='500'
                            src={`data:image/png;base64,${
                              kycInfo.contactPersonPassport
                                ? kycInfo.contactPersonPassport
                                : 'N/A'
                            }`}
                          />
                        </div>{' '}
                      </CCol>
                    </CFormGroup>}
                    <br />


                    </>}
                    <br />

                    {providerDetails &&
                    <button
                      type='button'
                      class='btn btn-primary'
                      onClick={viewProviderFunc}
                    >
                     
                        VIew Provider Details
                    </button>}
                  </CForm>
                </CCardBody>
                
              </CCard>
            </CCol>
          </CRow>
        </div>
      </Modal>
    </CRow>
  )
}

export default Users
